section {
  margin: 20px 20px 70px;
  @media #{$lg} {
    margin: 45px 60px 120px;
  }

  &.section_bg {
    padding-top: 45px;
    padding-bottom: 45px;

    &_light {
      background-color: $color-5;
    }
  }

  &.section_border {
    &_top {
      padding-top: 30px;
      border-top: $border;
      @media #{$lg} {
        padding-top: 0;
      }
    }

    &_bottom {
      border-bottom: $border;
    }

    &_btw {
      @media #{$lg} {
        .section__item {
          padding: 45px 0;

          &:first-child {
            padding-right: 50px;
          }

          &:not(:first-child) {
            padding-left: 50px;
            border-left: $border;
          }
        }
      }
    }
  }

  &.section_narrow {
    padding-left: 0;
    padding-right: 0;

    @media #{$lg} {
      padding-left: 115px;
      padding-right: 115px;
    }
  }

  &.section_x-narrow {
    padding-left: 0;
    padding-right: 0;

    @media #{$xl} {
      padding-left: 300px;
      padding-right: 300px;
    }

    @media #{$lg} {
      padding-left: 200px;
      padding-right: 200px;
    }
  }

  .section-title {
    margin-bottom: 15px;
    color: $color-4;
    font-size: $fs-xs;
    font-weight: 700;
    letter-spacing: $ls-wide;
    text-transform: uppercase;

    &_accent {
      margin-bottom: 30px;
      color: $color-2;
    }
  }
}

.cat-title {
  margin-top: 40px;
  margin-bottom: 15px;
  color: $color-4;
  font-size: $fs-xs;
  font-weight: 700;
  letter-spacing: $ls-wide;
  text-transform: uppercase;

  &_accent {
    color: $color-2;
  }
}