.accordion {
  h2 {
    padding: 1rem 1.25rem;
    font-weight: 500;
    font-size: 1rem;
    color: $color-1;
  }

  &-button {
    padding: 0;
    color: $color-2;
    font-weight: 500;

    &:after {
      width: .7rem;
      height: .7rem;
      background-image: url("../img/dropdown.svg");
      background-size: .7rem;
    }

    &:not(.collapsed) {
      color: $color-2;
      background-color: transparent;

      &:after {
        background-image: url("../img/dropdown.svg");
      }
    }

    span {
      margin-left: 5px;
      display: contents;
      vertical-align: baseline;
      font-weight: 400;
      font-size: $fs-xxs;
    }
  }

  .cat-title {
    &:first-child {
      margin-top: 0;
    }
  }
}

.card {
  display: flex;
  flex-direction: column;
  padding-bottom: 28px;
  margin-bottom: 36px;
  border-bottom: .75px solid $color-6;

  &:last-child {
    border-bottom: none;
  }

  //&:nth-last-child(2)

  @media #{$lg} {
    padding-right: 40px;
    flex-direction: row;
  }

  p {
    margin: 6px 0;

    &:first-child {
      margin-bottom: 12px;
    }
  }

  &__image {
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
    max-width: 100%;
    max-height: 270px;
    height: intrinsic;
    overflow: hidden;
    border-radius: 7px;

    @media #{$lg} {
      margin-right: 25px;
      margin-bottom: 0;
      max-width: 40%;
      max-height: unset;
      height: fit-content;
    }

    img {
      width: 100%;
      height: fit-content;
      align-self: center;

      @media #{$lg} {
        max-width: 350px;
        height: 100%;
      }
    }

    &_rounded {
      width: 200px;
      height: 200px;
      border-radius: 50%;
      max-width: unset;

      img {
        width: auto;
        height: 100%;
        align-self: unset;
      }
    }
  }

  &__content {
    max-width: 100%;

    @media #{$lg} {
      max-width: 60%;
    }

    @media #{$xl} {
      max-width: 70%;
    }
  }

  &__title {
    margin-bottom: $mb-sm;
    font-size: $fs-lg;
    font-weight: 700;
  }

  &__date {
    margin-bottom: $mb-base;
    color: $color-4;
    font-size: $fs-xs;
  }

  &__text {
    margin-bottom: $mb-sm;
  }

  &__button {
    &:before {
      content: "";
      display: inline-block;
      vertical-align: middle;
      width: 50px;
      height: 2px;
      background-color: $color-7;
      margin: 0 8px 0 0;
      transition: all .3s ease;
    }

    &:hover {
      &:before {
        margin: 0 16px 0 0;
        width: 60px;
        background-color: $color-1;
        transition: all .3s ease;
      }
    }
  }

  &_two-columns {
    margin-bottom: 0;
    padding: 36px 0;
    flex-direction: column;
    @media #{$lg} {
      padding: 36px 0;
    }

    &:nth-child(odd) {
      @media #{$lg} {
        border-right: .75px solid $color-6;
      }
    }

    &:nth-child(even) {
      @media #{$lg} {
        padding-left: 26px;
      }
    }
  }
}

.dropdown {
  margin: 0 auto;
  width: 100%;
  text-align: center;

  @media #{$md} {
    width: 70%;
  }

  &-toggle {
    margin: 0 auto;
    width: 100%;
    color: $color-2;
    border-color: $color-2;
    border-width: 1px;
    background-color: transparent;

    &:hover {
      color: $color-2;
    }
  }

  &-menu {
    width: 100%;
    color: $color-4;
    text-align: center;
    text-transform: uppercase;
    box-shadow: #a0a6ab66 0 0 25px 0;
    border: none;

    li {
      font-size: 13.5px;
      letter-spacing: 1px;

      &:before {
        display: none;
      }

      &:last-child {
        margin-bottom: 0;
      }

      &.current {
        a {
          font-weight: 600;
          color: $color-1;
        }
      }
    }
  }

  &-item {
    box-sizing: border-box;
    color: $color-4;
    font-weight: 600;

    &:hover {
      color: $color-4;
      background-color: transparent;
    }
  }
}

.frame-block {
  margin-top: 40px;
  padding: 24px;
  border: 2px solid $color-8;

  p:last-child {
    margin-bottom: 0;
  }

  h3 {
    margin-bottom: $mb-base;
  }

  &__categories {
    margin-bottom: 0;

    li {
      &:last-child {
        margin-bottom: 0;
      }

      &:before {
        display: none;
      }

      &.current {
        a {
          color: $color-2;
          font-weight: 500;
        }
      }

      a {
        color: $color-4;
        letter-spacing: 1px;
        font-weight: 600;
        font-size: $fs-sm;
        text-transform: uppercase;

        &:hover {
          margin-left: 10px;
        }
      }
    }
  }
}

.links-block {
  margin-top: 32px;

  a {
    margin-bottom: 12px;
    padding: 10px;
    display: inline-block;
    background-color: #ee63520a;
    border: 1px solid;
    border-radius: 2px;
    white-space: nowrap;

    &:hover {
      background-color: transparent;
    }

    &:not(:last-child) {
      margin-right: 15px;
    }
  }
}

.roadmap {
  table {
    margin-left: 30px;

    tr {
      box-sizing: border-box;
      width: 100%;

      .roadmap__title {
        font-size: $fs-lg;
        font-weight: 700;
        margin: 0 0 7px 8px;
      }

      .roadmap__text {
        padding-bottom: 36px;
        display: block;
      }

      &.roadmap__step {
        &_1 {
          th {
            &:before {
              content: url("/wp-content/themes/innovationfund/dist/img/step-1.svg")
            }
          }
        }

        &_2 {
          th {
            &:before {
              content: url("/wp-content/themes/innovationfund/dist/img/step-2.svg")
            }
          }
        }

        &_3 {
          th {
            &:before {
              content: url("/wp-content/themes/innovationfund/dist/img/step-3.svg")
            }
          }
        }

        &_4 {
          th {
            &:before {
              content: url("/wp-content/themes/innovationfund/dist/img/step-4.svg")
            }
          }
        }

        &_5 {
          th {
            &:before {
              content: url("/wp-content/themes/innovationfund/dist/img/step-5.svg")
            }
          }
        }

        &_6 {
          th {
            &:before {
              content: url("/wp-content/themes/innovationfund/dist/img/step-6.svg")
            }
          }
        }

        &_7 {
          th {
            &:before {
              content: url("/wp-content/themes/innovationfund/dist/img/step-7.svg")
            }
          }
        }
      }

      &:last-child {
        th {
          &:after {
            display: none;
          }
        }
      }

      th {
        position: relative;
        font-weight: $fw-base;

        &:before {
          position: absolute;
          top: 4px;
          left: -26px;
          content: '';
          width: 12px;
          height: 12px;
          border-radius: 10px;
        }

        &:after {
          position: absolute;
          top: 5px;
          left: -20px;
          content: '';
          width: 1px;
          height: 100%;
          z-index: -1;
        }
      }

      &.current, &.passed {
        th {
          &:before {
            background-color: $color-8;
          }
        }
      }

      &.passed {
        th {
          &:after {
            background-color: $color-8;
          }
        }
      }

      &.current, &.forthcome {
        th {
          &:after {
            background-color: $color-9;
          }
        }
      }

      &.forthcome {
        th {
          &:before {
            background-color: $color-9;
          }
        }
      }
    }
  }
}